<template>
	<div class="pa-md-6 pa-sm-3">
		<v-row>
			<v-col 
				v-for="(item, i) in dataDashboard" :key="i"
				cols="12" md="4" sm="12"
				class="pt-0">
				<v-sheet 
					:color="`${item.color} lighten-4`"
					class="pa-4">
					<div class="d-flex justify-space-between">
						<div class="display-1 white--text">
							{{ item.value }} <br>
							{{ item.text }}
						</div>
						<v-icon size="80" class="rgba--text">{{ item.icon }}</v-icon>
					</div>
				</v-sheet>
			</v-col>
		</v-row>
	</div>
</template>

<style scoped>
	.rgba--text{ color: rgba(0,0,0,0.2) !important; }
</style>

<script>
	import { mapState } from 'vuex'

	export default{
		data: () => ({
			items: [
				{ color: 'red', value: '300', text: 'Startups', icon: 'mdi-spa' },
				{ color: 'primary', value: '20', text: 'Incubators', icon: 'mdi-ballot' },
				// { color: 'orange', value: '100', text: 'Events', icon: 'mdi-ticket' }
			]
		}),

		created() {
			this.$store.dispatch('dataAdminIncubator/getCountIncubator')
			this.$store.dispatch('dataAdminStartup/getCountStartup')
		},

		computed: {
			...mapState('dataAdminIncubator', ['dataIncubator']),
			...mapState('dataAdminStartup', ['dataStartup']),
			dataDashboard() {
				return this.items.map(val => {
					if (val.text === 'Startups') {
						val.value = this.dataStartup
					} else if (val.text === 'Incubators') {
						val.value = this.dataIncubator
					}

					return val
				})
			}
		}
	}
</script>